<template>
  <div class="container">

    <div class="form-group">
      <label for="usr">Num :</label>
      <input type="text" class="form-control" id="cardNumberInput" placeholder="Card Number" v-on:focus="showFront"
        v-model="visa.num" :maxlength="12">
    </div>


    <div class="form-group">
      <label for="usr">Name :</label>
      <input type="text" class="form-control" id="otherInput" placeholder="Other Input" v-on:focus="showFront"
        v-model="visa.name" pattern="[A-Za-z\s]*" :maxlength="18">

    </div>


    <div class="form-group">
      <label for="usr">Date :</label>
      <input type="text" class="form-control" id="otherInput" v-on:focus="showFront" v-model="visa.exp_date"
        pattern="[0-9]{0,4}-" maxlength="5" placeholder="XXXX">
    </div>


    <div class="form-group">
      <label for="usr">CVC :</label>
      <input type="text" class="form-control" id="otherInput" v-on:focus="showBack" v-model="visa.cvc" pattern="[0-9]"
        maxlength="3" placeholder="XXXX">
    </div>



    <div class="row mb-4">
      <div id="cardContainer" class="col-10 col-sm-8 col-md-8 col-lg-6 col-xl-5 mx-auto">
        <div id="cardFlipper" :class="{ flipped: isFlipped }">
          <div id="cardFront" class="p-4">
            <!-- Front of the card content -->
            <div class="h3" style="display: flex; justify-content: space-between;">
              <div class="">VISA</div>
              <div class="">
                <img src="@/assets/master.png" alt="" width="60" height="35">
              </div>
            </div>
            <div class="ml-2 mt-1">
              <img src="@/assets/chip.png" alt="" width="50" height="50">
            </div>
            <div class="text-white visa-num h4 text-center mt-3">
              <!-- 1234-4567-791-569 -->
              <!-- {{ visa.num }} -->
              {{ formattedCardNumber }}
            </div>
            <div class="text-center" style="display: flex; justify-content: center;">
              <span>
                <div class="good">Good</div>
                <div class="thru">Thru</div>
              </span>
              <div class="ml-2">
                <!-- 12/05 -->
                {{ visa.exp_date }}
              </div>
            </div>
            <div class="mt-3 name h5">
              <!-- ELSHAYMAA NAHAS -->
              {{ visa.name }}
            </div>
          </div>
          <div id="cardBack">

            <hr class="mt-4" style="border: 18px solid black;">

            <div class="bg-white p-3 w-50 mx-auto mt-4" style="display: flex; justify-content: space-between;">
              <div class="visa-num-back">{{ visa.num }}</div>
              <div class="">{{ visa.cvc }}</div>

            </div>

            <hr class="mt-5 ml-4" style="border: 2px solid wheat; width: 50%;">
            <hr class=" ml-4" style="border: 2px solid wheat; width: 50%;">
            
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isFlipped: false,
      visa: {
        num: '123456781255',
        name: 'ELSHAYMAA NAHAS',
        exp_date: '05-25',
        cvc: '123'
      }
    }
  },
  methods: {
    showFront() {
      this.isFlipped = false;
    },
    showBack() {
      this.isFlipped = true;
    }
  }, computed: {
    formattedCardNumber() {
      const cardNumber = this.visa.num;
      const formattedNumber = [];

      for (let i = 0; i < cardNumber.length; i++) {
        if (i !== 0 && i % 4 === 0 && i !== 12) {
          formattedNumber.push('-');
        }
        formattedNumber.push(cardNumber[i]);
      }

      return formattedNumber.join('');
    }
  }
}
</script>

